@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro);
.header{display:grid;grid-template-columns:minmax(4%, 6rem) 1fr;align-items:center;background-color:#06d6a0}.header .menu-switcher{font-size:2rem;margin-left:2rem;cursor:pointer;color:#333}.header .header-title{display:grid;justify-items:center}.header .header-title h1{color:#333}


#sidebar-container{height:calc(100% - 5rem);z-index:1001;width:10%;position:fixed !important;display:grid;opacity:0;visibility:hidden;background-color:#333;transition:opacity 0.2s, visibility 0.2s;grid-template-rows:4% 1fr 5%;justify-content:center}.sidebar-footer{display:grid;padding-bottom:0.5rem;align-self:end;color:#06d6a0}

#map{height:100%}

.container{display:grid;align-items:stretch;justify-items:stretch;justify-content:stretch;align-content:stretch}

#root{display:grid;height:100vh;grid-template-rows:5rem 1fr}

*{margin:0;padding:0}*,*::before,*::after{box-sizing:inherit}html{box-sizing:border-box;font-size:62.5%}body{font-family:"Source Sans Pro", sans-serif}

